.task-wrapper {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  margin: 10px 0;
}
.task-wrapper img {
  width: 40px;
  height: 40px;
  min-width: 40px;
  min-height: 40px;
  background-color: #404040;
  border-radius: 50%;
  margin: 5px 5px 0 0;
}
.task-wrapper .task {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.task-wrapper .task-priority {
  width: 15px;
  height: 100%;
  background-color: red;
  border-radius: 15px 0 0 15px;
}
.task-wrapper .task-check-box {
  box-sizing: border-box;
  width: 20px;
  height: 100%;
  background: linear-gradient(180deg, rgb(40, 75, 255) 0%, rgb(96, 169, 255) 100%);
  border-left: 0;
  border-radius: 0 15px 15px 0;
}
.task-wrapper .task-check-box:hover {
  background: linear-gradient(180deg, rgb(0, 255, 55) 0%, rgb(3, 159, 0) 100%);
}
.task-wrapper .task .task-inner {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  align-items: center;
  box-sizing: border-box;
  padding: 10px 0 0 0;
  --task-bg-color: var(--tg-theme-secondary-bg-color);
  background-color: var(--task-bg-color, #404040);
}
.task-wrapper .task .task-inner p {
  height: auto;
  box-sizing: border-box;
  width: 100%;
  padding: 0 10px 5px 10px;
  text-wrap: normal;
  text-align: start;
  margin: 0;
}
.task-wrapper .task .task-inner .task-button-menu {
  justify-content: space-between;
  width: 100%;
  display: flex;
  height: 15px;
}
.task-wrapper .task .task-inner .task-button-menu button {
  padding: 0;
  margin: 0;
  border: 0;
  width: 33.3333%;
  font-size: 12px;
}
.task-wrapper .task .task-inner .task-button-menu button:first-child {
  background-color: #7ece01;
}
.task-wrapper .task .task-inner .task-button-menu button:nth-child(2) {
  background-color: yellow;
}
.task-wrapper .task .task-inner .task-button-menu button:last-child {
  background-color: #f63f3f;
}

